body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.g-navbar-brand-link,
.g-navbar-brand-link:hover {
  text-decoration: none;
  color: inherit;
}

.g-card {
  margin: 20px;
}

.g-card-image {
  border-radius: 10px;
  width: 300px;
  height: 480px;
  box-shadow: 0px 0px 3px 1px #ccc;
  object-fit: cover;
}

.g-card-image:hover {
  cursor: pointer;
}

.g-card-title {
  font-size: 24px;
  margin: 0px;
}

.g-card-info {
  margin-top: 10px;
  min-height: 100px;
}

.g-card-sub-title {
  font-size: 16px;
  margin: 0px;
}

.success-msg {
  color: #57a773;
  padding: 10px 15px;
}

.error-msg {
  color: #ee6352;
  padding: 10px 15px;
}
